@import '/src/common/scss/common.scss';
@import '/src/components/logo/logo.scss';
@import '/src/components/header/header.scss';

.movies.section {
  min-height: 100%;
  padding: 32px 0 100px;

  background-color: #212328;
}
.movies__container {
  display: flex;
  flex-direction: column;
  gap: 32px;
  max-width: 1440px;
  padding: 0 80px;
  margin: 0 auto;
}
.filters {
  display: flex;
  flex-direction: column;
  gap: 36px;
}
.filters__box {
  display: flex;
  gap: 16px;
}
.filters-category__list {
  display: flex;
  gap: 40px;
}
.filters-category__btn {
  position: relative;
  color: #fff;
  font-weight: 700;
  line-height: normal;

  background-color: transparent;
}
.filters-category__btn.current::before {
  content: '';
  position: absolute;
  bottom: -4px;

  display: block;
  width: 100%;
  height: 1px;

  background: #fff;
}

.filters-dropdown__list {
  position: relative;
  display: flex;
  flex-direction: column;

  border: 1px solid #fff;
}
.filters-dropdown__list::before {
  content: '';
  position: absolute;
  top: 8px;
  right: 16px;

  display: block;
  width: 16px;
  height: 16px;
  transition: 300ms linear;

  background-image: url('/static/images/filter-arrow.svg');
}
.filters-dropdown__list.isActive::before {
  // display: none;
  transform: rotate(180deg);
}
.filters-dropdown__item {
  display: flex;
  width: 100%;
  padding: 8px 16px;
  align-items: center;

  font-size: 12px;
  line-height: normal;
  color: #fff;

  .filters-dropdown__list.isActive &:not(:first-of-type) {
    border-top: 1px solid #fff;
  }
  &.isHidden {
    display: none;
  }

  &:hover {background: #464646;}
}

.filters-genre {
  position: relative;
  width: 200px;
  min-height: 32px;
}
.filters-year {
  position: relative;
  width: 92px;
  min-height: 32px;
}
.filters-country {
  position: relative;
  width: 184px;
  min-height: 32px;
}
.filters-perpage {
  position: relative;
  width: 48px;
  min-height: 32px;
}


.filters-genre__list {
  position: absolute;
  width: 200px;

  background-color: #212328;
  z-index: 2;
}
.filters-year__list {
  position: absolute;
  width: 92px;

  background-color: #212328;
  z-index: 2;
}
.filters-country__list {
  position: absolute;
  width: 184px;

  background-color: #212328;
  z-index: 2;
}
.filters-perpage__list {
  position: absolute;
  width: 48px;

  background-color: #212328;
  z-index: 2;
}
.filters-perpage__list.filters-dropdown__list::before {
  right: 8px;
}
.filters-perpage__item.filters-dropdown__item {
  padding: 8px 24px 8px 8px;
}

.filters-perpage {
  margin-left: auto;
}

.movies__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  --gap: 16px;
  gap: var(--gap);
}
.movies__item {
  --items: 6;
  flex-basis: calc((100% - var(--gap) * (var(--items) - 1)) / var(--items));
}
.movie {
  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
  
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    opacity: 0;
    transition: all 300ms ease-out;
  }
}
.movie:hover::before,
.movie:focus::before { opacity: 1;}

.movie__img-wrap {
  position: relative;
}
.movie__img-wrap::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  display: block;
  width: 72px;
  height: 72px;

  background-image: url('/static/images/play.svg');
  transition: all 300ms ease-out;
  z-index: 1;
  opacity: 0;
}
.movie:hover .movie__img-wrap::before,
.movie:focus .movie__img-wrap::before { opacity: 1;}


.movie__img {
  width: 100%;
  height: auto;
  object-fit: cover;
}
.movie__viewing,
.movie__rating {
  position: absolute;
  top: 16px;

  display: flex;
  width: 77px;
  height: 24px;
  padding: 4px 16px 4px 8px;
  justify-content: center;
  align-items: center;
  gap: 10px;

  color: #fff;
  font-size: 14px;
  line-height: normal;

  background: #464646;
}

.movie__viewing {
  border-radius: 0px 16px 16px 0px;
}
.movie__rating {
  right: 0;
  border-radius: 16px 0px 0px 16px;
}
.movie__btn {
  position: absolute;
  bottom: 16px;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;

  color: #fff;

  border-radius: 50%;
  background: #464646;
}
.movie__btn-card {
  left: 16px;
}
.movie__btn-users {
  left: calc(16px + 8px + 40px);
}
.movie__desc {
  padding: 8px 16px;
}
.movie__title {
  margin-bottom: 8px;

  color: #fff;
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
}

.movie__year,
.movie__country {
  color: #a8a8a8;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}

.movie__year::after {
  content: ',';
  color: #a8a8a8;
  font-size: 14px;
  font-weight: 400;
  line-height: normal;
}

.pagination {
  margin: 105px auto 0;
}
.pagination__list {
  display: flex;
  gap: 8px;
}
.pagination__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 24px;
  min-width: 24px;
  // max-width: 48px;
  padding: 0 8px;

  color: #fff;
  font-size: 14px;
  line-height: normal;
  background: #464646;
}
